@import url(https://fonts.googleapis.com/css2?family=Cairo&family=Courier+Prime&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;700;800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Cairo&family=Courier+Prime&display=swap);
.ip {
  max-width: 50vw;
  background: transparent;
  border: solid 1px white;
  border-radius: 30px;
  height: 8vh;
}
.ip:focus {
  background: transparent;
  color: whitesmoke;
}
.submit-button {
  display: block;
  font-family: "Courier prime", monospace;
  letter-spacing: 1px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}
.card {
  box-shadow: 3px 3px 0px rgba(243, 223, 223, 0.966);
  margin: 10px;
}
.images {
  max-width: 45vw;
  max-height: 35vh;
}
@media screen and (max-width: 770px) {
  .images {
    max-width: 35vw;
    max-height: 35vh;
  }
}

::-webkit-scrollbar {
  width: 6px;
}
::-webkit-scrollbar-track {
  background-color: #1f1d48;
}
::-webkit-scrollbar-thumb {
  background: #fff;
  border-radius: 10px;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.container-m {
  background-image: url(/static/media/home-bg.fb09e6ab.webp);
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  max-width: 100vw;
  margin: 40px 0;
}

.container-m .card {
  position: relative;
  min-width: 320px;
  height: 440px;
  -webkit-backdrop-filter: blur(12px) saturate(165%);
          backdrop-filter: blur(12px) saturate(165%);
  background-color: rgba(17, 25, 40, 0.64);
  border-radius: 12px;
  border: 1px solid rgba(255, 255, 255, 0.125);

  border-radius: 15px;
  margin: 30px;
  transition: 0.5s;
}

.container-m .card:nth-child(1) .box .content a {
  background: #2196f3;
}

.container-m .card:nth-child(2) .box .content a {
  background: #e91e63;
}

.container-m .card:nth-child(3) .box .content a {
  background: #23c186;
}
.container-m .card:nth-child(4) .box .content a {
  background: #e9db13;
}

.container-m .card:nth-child(5) .box .content a {
  background: #aa0eda;
}

.container-m .card:nth-child(6) .box .content a {
  background: #df8b2c;
}
.container-m .card:nth-child(7) .box .content a {
  background: #2196f3;
}
.container-m .card:nth-child(8) .box .content a {
  background: #e91e63;
}
.container-m .card:nth-child(9) .box .content a {
  background: #aa0eda;
}
.container-m .card .box {
  position: absolute;
  top: 20px;
  left: 20px;
  right: 20px;
  bottom: 20px;
  border-radius: 15px;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  transition: 0.5s;
}

.container-m .card .box:hover {
  -webkit-transform: translateY(-20px);
          transform: translateY(-20px);
}

.container-m .card .box:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 50%;
  height: 100%;
  background: rgba(255, 255, 255, 0.03);
}

.container-m .card .box .content {
  padding: 20px;
  text-align: center;
}

.container-m .card .box .content h2 {
  position: absolute;
  top: -10px;
  right: 30px;
  font-size: 8rem;
  color: rgba(255, 255, 255, 0.1);
}

.container-m .card .box .content h3 {
  font-size: 1.8rem;
  color: #fff;
  z-index: 1;
  transition: 0.5s;
  margin-bottom: 15px;
}

.container-m .card .box .content p {
  font-size: 2.5rem;
  font-weight: 800;
  font-family:"cairo",sans-serif;
  color: rgba(255, 255, 255, 0.9);
  z-index: 1;
  transition: 0.5s;
}

.container-m .card .box .content a {
  position: relative;
  display: inline-block;
  padding: 8px 20px;
  background: black;
  border-radius: 5px;
  text-decoration: none;
  color: white;
  margin-top: 20px;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
  transition: 0.5s;
}
.container-m .card .box .content a:hover {
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.6);
  background: #fff;
  color: #000;
}

body {
  background-image: url(/static/media/home-bg.fb09e6ab.webp);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  overflow-x: hidden;
}
.home_image {
  width: 50vw;
  margin-bottom: 10px;
}
.home-div {
  position: relative;
  height: 92vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  overflow: hidden;
}
#boy {
  position: absolute;
  width: 35vw;
  left: -2rem;
  bottom: -1rem;
  height: 40vh;
  z-index: -200;
}
.login_button {
  background-color: rgb(243, 55, 218);
  font-family: "Courier prime", monospace;
  font-size: 2rem;
  background-image: linear-gradient(
    -225deg,
    #2cd8d5 0%,
    #c5c1ff 56%,
    #ffbac3 100%
  );
  box-shadow: none;
  letter-spacing: 1px;
}
.login_button:hover,
.login_button:active {
  box-shadow: 0px 0px 10px 5px #0ff;
}

.glass {
  position: absolute;
  top: 0px;
  left: 0px;
  height: 100vh;
  width: 400px;
  backdrop-filter: blur(10px) saturate(180%);
  -webkit-backdrop-filter: blur(10px) saturate(180%);
  background-color: rgba(17, 25, 40, 0.1);
  border-radius: 12px;
  font-family: "cairo", sans-serif;
  border: 2px solid rgba(42, 91, 196, 0.952);
  z-index: 10040;
  color: white;
}
.login-css {
  backdrop-filter: blur(25px) saturate(200%);
  -webkit-backdrop-filter: blur(25px) saturate(200%);
  background-color: rgba(17, 25, 40, 0.5);
  border-radius: 12px;
  border: 2px solid rgba(42, 91, 196, 0.952);
}
.modal-body li {
  font-size: large;
}
@media screen and (max-width: 800px) {
  .home_image {
    max-height: 300px;
    max-width: 500px;
    margin-bottom: 20px;
  }
}
@media screen and (max-width: 520px) {
  .home_image {
    max-height: 300px;
    max-width: 400px;
    margin-bottom: 20px;
  }
  #boy {
    display: none;
  }
  .glass {
    width: 270px;
  }
}

